import React, { Component } from "react";
import "./vendor/bootstrap/css/bootstrap.min.css";
// import "./fonts/iconic/css/material-design-iconic-font.min.css";
// import "./fonts/font-awesome-4.7.0/css/font-awesome.min.css";
import "./vendor/css-hamburgers/hamburgers.min.css";
import "./vendor/animate/animate.css";
import "./vendor/select2/select2.min.css";
import "./vendor/animsition/css/animsition.min.css";
import "./vendor/daterangepicker/daterangepicker.css";
import "./css/util.css";
import "./css/main.css";
import main from "./js/main.js";
//import worker from "./worker.js";
//import WebWorker from "./workerSetup";
// import cog from "./images/cogwheels.svg";
// import StegIcon from "./images/stegicon.svg";
import GitHubButton from "react-github-btn";
// import you from "./images/youtube_social_icon_dark.png"
// const StegCloak = require('stegcloak');
import StegCloak from "stegcloak";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      esecret: "我的秘密",
      epass: "",
      encrypt: true,
      hmac: false,
      emsg: "你好 这是一条机密信息",
      rmsg: "",
      dpass: "",
      dmsg: "",
      dsecret: "秘密",
      msgbox: "MESSAGE *",
      btn: "隐藏",
      progress: false,
      err: "",
    };
  }
  componentDidMount() {
    //this.worker = new WebWorker(worker);
    main();
  }
  hidemsg = (e) => {
    var { esecret, epass, emsg, hmac, encrypt } = this.state;
    if (epass == "" && encrypt) {
      let pass = document.getElementById("password");
      pass.setCustomValidity("请添加密码");
      return;
    }
    if (
      !emsg.includes(" ") ||
      (emsg.includes(" ") && emsg.split(" ")[1].length == 0)
    ) {
      let msg = document.getElementById("message");
      msg.setCustomValidity("封面信息应至少包含2个单词！");
      return;
    }
    e.preventDefault();
    if (this.state.btn == "隐藏") {
      let data = {};
      data["message"] = esecret;
      data["key"] = epass;
      if (!emsg.includes(" ")) {
        emsg = emsg + " ";
      }
      data["cover"] = emsg;
      this.setState({ progress: true });
      //this.worker.postMessage({ enc: true, data, hmac, encrypt });
      const stegcloak = new StegCloak(encrypt, hmac);
      let hidden = stegcloak.hide(data.message, data.key, data.cover)
      this.setState({ rmsg: hidden, btn: "清除", progress: false });
      // this.worker.addEventListener("message", (e) => {
      //   if (e.data.enc) {
      //     let hidden = e.data.emsg;
      //     this.setState({ rmsg: hidden, btn: "Clear", progress: false });
      //   }
      // });
    } else {
      this.setState({ btn: "隐藏", rmsg: "", emsg: "" });
    }
  };
  demsg = (e) => {
    e.preventDefault();
    // this.worker.postMessage({
    //   enc: false,
    //   dmsg: this.state.dmsg,
    //   pass: this.state.dpass,
    // });
    // this.worker.addEventListener("message", (e) => {
    //   if (!e.data.enc) {
    //     this.setState({ dsecret: e.data.pass });
    //   }
    // });
    const stegcloak = new StegCloak();  // Initializes with encryption true and hmac false for hiding
    const pass = stegcloak.reveal(this.state.dmsg, this.state.dpass);
    this.setState({ dsecret: pass });
  };
  copyClip = (e) => {
    e.preventDefault();
    let clip = document.getElementById("res");
    clip.select();
    clip.setSelectionRange(0, 99999);
    document.execCommand("copy");
  };
  docs = (cl) => {
    return document.querySelector(cl);
  };
  retRes = () => {
    return (
      <div style={{ width: "inherit" }}>
        <div className="flex-sb label-input100">
          <label className="" htmlFor="res">
            加密后的信息*
          </label>
          <button
            style={{ width: "20px", height: "25px" }}
            onMouseEnter={() => (this.docs(".copy").style.opacity = 1)}
            onMouseLeave={() => (this.docs(".copy").style.opacity = 0)}
            id="clip"
            className="m-l-10 "
            onClick={(e) => this.copyClip(e)}
          >
            <svg
              aria-hidden="true"
              data-prefix="far"
              data-icon="copy"
              role="img"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 448 512"
            >
              <path d="M433.941 65.941l-51.882-51.882A48 48 0 0 0 348.118 0H176c-26.51 0-48 21.49-48 48v48H48c-26.51 0-48 21.49-48 48v320c0 26.51 21.49 48 48 48h224c26.51 0 48-21.49 48-48v-48h80c26.51 0 48-21.49 48-48V99.882a48 48 0 0 0-14.059-33.941zM266 464H54a6 6 0 0 1-6-6V150a6 6 0 0 1 6-6h74v224c0 26.51 21.49 48 48 48h96v42a6 6 0 0 1-6 6zm128-96H182a6 6 0 0 1-6-6V54a6 6 0 0 1 6-6h106v88c0 13.255 10.745 24 24 24h88v202a6 6 0 0 1-6 6zm6-256h-64V48h9.632c1.591 0 3.117.632 4.243 1.757l48.368 48.368a6 6 0 0 1 1.757 4.243V112z"></path>
            </svg>
          </button>
          <div className="copy">
            <span>复制到剪贴板</span>
          </div>
        </div>
        <div style={{ marginBottom: "15px" }} className="wrap-input100 ">
          <textarea
            id="res"
            className="input100"
            name="message"
            value={this.state.rmsg}
            readOnly={true}
          ></textarea>
          <span className="focus-input100"></span>
        </div>
        <p className="d-input100">复制并粘贴到任何位置.....</p>
      </div>
    );
  };
  retBtn = () => {
    return (
      <div className="container-contact100-form-btn m-b-25">
        <button
          className="contact100-form-btn validate-form"
          onClick={(e) => this.hidemsg(e)}
        >
          <span>
            {this.state.btn}
            <i className="zmdi zmdi-arrow-right m-l-8"></i>
          </span>
        </button>
      </div>
    );
  };

  render() {
    return (
      <div className="container-contact100">
        <div style={{ position: "relative" }} className="wrap-contact100">
          <form className="contact100-form validate-form">
            <p className=" p-input100 text-center m-t-35"> 隐藏秘密 </p>
            <hr className="hr-input100 m-t-2" />
            <div className="rs1 next">
              <label className="fs-18 text-center p-b-11" htmlFor="first-name">
                秘密{" "}
              </label>
              <div className="wrap-input100 validate-input">
                <input
                  id="first-name"
                  className="input100 text-center"
                  type="text"
                  name="first-name"
                  placeholder="我的秘密"
                  onChange={(e) => this.setState({ esecret: e.target.value })}
                />
                <span className="focus-input100"></span>
              </div>
            </div>
            <div className="rs1 next">
              <label className="fs-18 text-center p-b-11" htmlFor="password">
                密码
              </label>
              <div className="wrap-input100  validate-input">
                <input
                  id="password"
                  className="input100 text-center"
                  type="password"
                  required="required"
                  disabled={!this.state.encrypt}
                  name="password"
                  placeholder="密码"
                  onChange={(e) => this.setState({ epass: e.target.value })}
                />
                <span className="focus-input100"></span>
              </div>
            </div>

            <div style={{ cursor: "pointer", textAlign: "left" }} id="adv1">
            </div>
            <label className=" label-input100 m-t-10 " htmlFor="message">
              展示信息
            </label>
            <div className="wrap-input100 validate-input">
              <textarea
                id="message"
                className="input100"
                name="message"
                placeholder="你好 这是一条机密信息"
                onChange={(e) => this.setState({ emsg: e.target.value })}
                value={this.state.emsg}
              ></textarea>
              <span className="focus-input100"></span>
            </div>

            {this.retBtn()}
            {/* {(this.state.progress)?this.retProg():this.retBtn()} */}
            {this.state.btn === "清除" ? this.retRes() : null}

            <p className=" p-input100 text-center m-t-35">显示秘密 </p>
            <hr className="hr-input100 m-t-2" />
            <div className="label-input100 flex-sa"></div>
            <label className="label-input100 m-l-2" htmlFor="pass">
              密码
            </label>

            <div className="wrap-input100 ">
              <input
                id="pass"
                className="input100"
                type="text"
                name="first-name"
                placeholder="密码"
                onChange={(e) => this.setState({ dpass: e.target.value })}
              />
              <span className="focus-input100"></span>
            </div>
            <label className=" label-input100 m-l-2" htmlFor="mess">
              加密后的信息 *
            </label>
            <div className="wrap-input100 ">
              <textarea
                id="mess"
                className="input100"
                name="mess"
                placeholder="请输入加密后的信息"
                onChange={(e) => this.setState({ dmsg: e.target.value })}
              ></textarea>
              <span className="focus-input100"></span>
            </div>
            <div className="container-contact100-form-btn">
              <button
                className="contact100-form-btn"
                onClick={(e) => this.demsg(e)}
              >
                <span>
                  获取秘密
                  <i className="zmdi zmdi-arrow-right m-l-8"></i>
                </span>
              </button>
            </div>
            <label className=" label-input100 m-l-2 m-t-10">你的秘密</label>
            <p className=" p-input100 text-center m-t-10 wraptext">
              <i> {this.state.dsecret} </i>
            </p>
          </form>
        </div>
      </div>
    );
  }
}

export default App;
